/*header css*/
.container {
  width: 95%;
  max-width: 1440px;
}

header {
  height: 100px;
  border-bottom: 3px solid #000;
  padding: 20px 0;
}
header .header_wrap {
  margin: 0 auto;
  display: flex;
  align-items: center;
}
header .header_wrap > div {
  width: 50%;
}
.link_area {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.link_area a {
  font-size: 17px;
  font-weight: 500;
  margin-right: 100px;
  position: relative;
}

.link_area a:hover:after {
  content: "";
  width: 100%;
  height: 4px;
  background: #3dee2b;
  display: block;
  position: absolute;
  bottom: -4px;
  border-radius: 2px;
}

.link_area a.selected:after {
  content: "";
  width: 100%;
  height: 4px;
  background: #3dee2b;
  display: block;
  position: absolute;
  bottom: -4px;
  border-radius: 2px;
}

/*visual css*/
.site_visual_wrap {
  width: 100%;
  height: 35vw;
  max-height: 640px;
  background: url("img/main_visual_bg.jpg") no-repeat center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}
.site_visual {
  height: 22.13541666666667vw;
  max-height: 425px;
  background: #fff;
  border: 5px solid #000;
  padding: 3.645833333333333vw 130px;
  display: flex;
  justify-content: space-between;
}
.site_visual_info h1 {
  font-size: 48px;
  color: #000;
  margin-bottom: 48px;
}
.site_visual_info_inner p {
  font-size: 22px;
  color: #000;
  line-height: 2;
}

.site_visual_img img {
  height: 100%;
}

/*contents list css*/
.list_contents_frame {
  margin: 120px auto;
}
.list_contents_frame h2 {
  text-align: center;
  font-size: 40px;
  margin-bottom: 80px;
  color: #000;
  line-height: 1;
}
.box_style_list li {
  width: calc(33.33% - 80px);
  margin-right: 80px;
  float: left;
  border: 3px solid #000;
  box-shadow: 35px 35px 0 #000;
  position: relative;
}
.box_style_list li:nth-child(3n) {
  margin-right: 0;
}
.box_style_list li.set-margin {
  margin-top: 80px;
}
.box_style_list li a {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  display: none;
}
.box_style_list li img {
  height: 100%;
}
.box_style_list li a div {
}
.box_style_list li a .title_p {
  font-size: 24px;
  font-weight: 800;
  color: #fff;
  margin-bottom: 35px;
}
.box_style_list li a .sub_p {
  font-size: 17px;
  color: #fff;
  font-weight: 500;
}

.box_style_list li:hover a {
  display: flex;
}

/*footer css*/
.foot_area {
  width: 100%;
  text-align: center;
  background: #333333;
  padding: 40px 0;
}
.foot_area p {
  font-size: 17px;
  color: #fff;
}

.subpage_visual {
  height: 640px;
  background: url("img/subpage_visual.png") no-repeat center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.subpage_visual_inner {
  border: none;
  background: url("img/subpage_visual_inner.png") no-repeat center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.subpage_visual_inner h2 {
  font-size: 70px;
  color: #fff;
  text-align: center;
}

.subpage_contents {
  margin: 0 auto;
  padding: 120px 0;
}

.image_with_title {
  text-align: center;
}

.image_with_title h3 {
  font-size: 40px;
  color: #000;
  margin-bottom: 80px;
}

.image_with_image img {
  max-width: 100%;
}

.content_down_low {
  padding: 110px;
  border: 4px solid #000;
}

.progress_bar {
  display: flex;
  align-items: center;
}

.content_down_low_list .progress_bar p {
  margin-bottom: 0;
  margin-left: 20px;
}

.progress_bar_frame {
  width: 80%;
  max-width: 610px;
  height: 50px;
  border: 3px solid #000;
  position: relative;
  background: #fff;
  overflow: hidden;
}

.progress_bar_frame .progress_bar_fill {
  background: #000;
  height: 100%;
  width: 100%;
}

.content_down_low_list {
  margin-bottom: 100px;
}

.content_down_low_list h4 {
  font-size: 23px;
  color: #000;
  margin-bottom: 30px;
}

.content_down_low_list h4 img {
  margin-right: 10px;
  vertical-align: middle;
}

.content_down_low_list p {
  font-size: 17px;
  color: #000;
  margin-bottom: 20px;
}

.open_links a {
  display: inline-block;
  padding: 10px 45px;
  font-size: 15px;
  border: 3px solid #000;
  line-height: 1;
}

.open_links a:hover {
  background: gray;
}

.open_links a:nth-child(1) {
  margin-right: 30px;
}

.open_links a.black {
  background: #000;
  color: #fff;
}

.open_links a.black:hover {
  background: gray;
  color: black;
}

/*swiper custom*/
.swiper-button-prev {
  background: url("img/arrow-left.png") no-repeat center;
  background-size: cover;
}
.swiper-button-next {
  background: url("img/arrow-right.png") no-repeat center;
  background-size: cover;
}

.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  display: none;
}

.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  display: none;
}

.swiper-pagination {
  bottom: 30px !important;
}
.swiper-pagination span {
  width: 30px;
  height: 32px;
  background: url("img/pagination.png") no-repeat center;
  background-size: 100%;
  opacity: 0.5;
  border-radius: 0;
}

.swiper-pagination span:nth-child(2) {
  background: url("img/pagination2.png") no-repeat center;
}
.swiper-pagination span:nth-child(3) {
  background: url("img/pagination3.png") no-repeat center top;
}

.swiper-pagination span.swiper-pagination-bullet-active {
  opacity: 1;
}

.three-slides .swiper-pagination span:nth-child(4),
.three-slides .swiper-pagination span:nth-child(5),
.three-slides .swiper-pagination span:nth-child(6) {
  display: none;
}

.two-slides .swiper-pagination span:nth-child(3),
.two-slides .swiper-pagination span:nth-child(4),
.two-slides .swiper-pagination span:nth-child(5),
.two-slides .swiper-pagination span:nth-child(6) {
  display: none;
}

/*미디어쿼리 영역*/
@media (max-width: 1600px) {
  .site_visual_info h1 {
    margin-bottom: 35px;
  }
  .box_style_list li {
    overflow: hidden;
    text-align: center;
  }

  .subpage_visual {
    height: 40vw;
  }
  .subpage_visual_inner h2 {
    font-size: 5vw;
  }
}

@media (max-width: 1440px) {
  .site_visual_info h1 {
    font-size: 3.5vw;
    margin-bottom: 1.5vw;
  }
  .site_visual_info_inner p {
    font-size: 1.5vw;
  }

  .subpage_contents {
    padding: 80px 0;
  }

  .image_with_title h3 {
    font-size: 30px;
  }

  .content_down_low {
    padding: 70px;
  }
}

@media (max-width: 1250px) {
  header {
    height: 10vw;
    padding: 2vw 0;
  }
  .logo_area a img {
    height: 6vw;
  }

  .link_area a {
    margin-right: 4vw;
  }

  .site_visual {
    padding: 3.645833333333333vw 80px;
  }

  .list_contents_frame {
    margin: 7vw auto;
  }
  .list_contents_frame h2 {
    margin-bottom: 4vw;
    font-size: 3vw;
  }
  .box_style_list li {
    margin-right: 4vw;
    width: calc(33.33% - 4vw);
    box-shadow: 2vw 2vw 0 #000;
  }
  .box_style_list li.set-margin {
    margin-top: 4vw;
  }

  .box_style_list li a .title_p {
    margin-bottom: 20px;
    font-size: 20px;
  }
  .box_style_list li a .sub_p {
    font-size: 15px;
  }

  .foot_area {
    padding: 2vw 0;
  }
  .foot_area p {
    font-size: 13px;
  }

  .subpage_contents {
    padding: 50px 0;
  }

  .content_down_low_list {
    margin-bottom: 50px;
  }
}

@media (max-width: 1050px) {
  .link_area a {
    font-size: 2vw;
  }
  .list_contents_frame {
    margin: 5vw auto 10vw;
  }
  .list_contents_frame h2 {
    font-size: 3.5vw;
  }

  .box_style_list li {
    width: calc(50% - 4vw);
    margin-right: 4vw;
  }
  .box_style_list li:nth-child(3n) {
    margin-right: 4vw;
  }
  .box_style_list li:nth-child(2n) {
    margin-right: 0;
  }
  .box_style_list li.set-margin {
    margin-top: 4vw;
  }
  .box_style_list li:nth-child(3) {
    margin-top: 4vw;
  }

  .foot_area {
    font-size: 3.5vw;
  }

  .image_with_image img {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .link_area a {
    font-size: 2.5vw;
  }

  .site_visual_wrap {
    height: auto;
    max-height: none;
    padding: 8vw 6vw;
  }
  .site_visual {
    height: auto;
    border: 3px solid #000;
    display: block;
    padding: 8vw;
  }
  .site_visual_img {
    text-align: right;
  }
  .site_visual_img img {
    height: 10vw;
  }
  .site_visual_info {
    display: flex;
    margin-bottom: 4vw;
    align-items: center;
  }
  .site_visual_info h1,
  .site_visual_info .site_visual_info_inner {
    width: 50%;
  }
  .site_visual_info h1 {
    font-size: 10vw;
  }
  .site_visual_info_inner p {
    font-size: 3vw;
    text-align: left;
  }

  .list_contents_frame {
    margin: 40px auto 50px;
  }
  .list_contents_frame h2 {
    font-size: 30px;
    margin-bottom: 35px;
  }

  .image_with_title h3 {
    margin-bottom: 50px;
  }

  .content_down_low {
    padding: 50px;
  }

  .open_links {
    text-align: center;
  }
  .open_links a {
    width: calc(50% - 10px);
    padding: 10px 0;
    text-align: center;
  }

  .open_links a:nth-child(1) {
    margin-right: 10px;
  }
}

@media (max-width: 500px) {
  .link_area a {
    font-size: 3vw;
  }
  .site_visual_wrap {
    padding: 7vw 5vw;
  }
  .site_visual {
    padding: 5vw 10vw;
  }
  .site_visual_info h1 {
    font-size: 35px;
  }
  .site_visual_info_inner p {
    font-size: 14px;
    text-align: left;
  }

  .list_contents_frame {
    margin: 30px auto 45px;
  }
  .list_contents_frame h2 {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .image_with_title h3 {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .content_down_low_list {
    margin-bottom: 30px;
  }

  .content_down_low {
    padding: 30px;
  }
  .content_down_low_list h4 {
    font-size: 17px;
    margin-bottom: 15px;
  }

  .content_down_low_list h4 img {
    max-width: 20px;
    vertical-align: top;
  }

  .content_down_low_list p {
    font-size: 14px;
    margin-bottom: 10px;
  }

  .progress_bar_frame {
    height: 35px;
  }

  .content_down_low_list .progress_bar p {
    font-size: 14px;
    margin-left: 10px;
  }
}

@media (max-width: 420px) {
  .site_visual_info h1 {
    font-size: 30px;
  }
}
